                {{ toUpperTextKey('enable') }}
                <v-icon x-small right class="mr-1" color="primary" @click="openMassEditDialog('enable'), $event.stopPropagation()">mdi-pencil</v-icon>
              </template>
              <template v-slot:header.writable="{ item }">
                {{ toUpperTextKey('writable') }}
                <v-icon x-small right class="mr-1" color="primary" @click="openMassEditDialog('writable'), $event.stopPropagation()">mdi-pencil</v-icon>
              </template>
              <template v-slot:header.write_value_once="{ item }">
