        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <!-- Create/Edit device dialog-->
    <v-dialog v-model="dialogDevice" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formDeviceTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12">
                  <v-text-field v-model="editedDevice.name" label="device name" required :rules="[() => !isExistingDevicevalue(editedDevice.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDevice">Cancel</v-btn>
          <v-btn color="blueen-1" text @click="saveDevice(editedDevice)" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import device wizard dialog-->
    <v-dialog v-model="dialogImportDevice" max-width="800px">
      <v-toolbar flat>
        <v-toolbar-title>Import Devices Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeDialogImportDevice">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1Device">
        <v-stepper-header>
          <v-stepper-step :complete="e1Device > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1Device > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImportDevices"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinueImportDevice()" :disabled="!dataImportDevices"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArrayDevice"
                class="elevation-1 mb-4"
                item-key="_id"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizardDevice"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>Import Devices Table</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizardDevice" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="onClickDeleteImportDevice(item)" v-if="!isClickSaveImportDevice"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImportDevice && !isExistingDevicevalue(item._id, '_id')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="#8cd0ab" v-if="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="saveDevice(item)" v-if="isClickSaveImportDevice" :disabled="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item }">
                  <v-text-field v-if="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line required :rules="[() => !isExistingDevicevalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1Device = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImportDevice" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Delete Device dialog -->
    <confirm-dialog :is-open="dialogDeleteDevice" :on-close="closeDeleteDevice" :on-confirm="confirmDeleteDevice" dialog-text="Do you want to delete the device ?"></confirm-dialog>

    <!-- Import wizard dialog-->
